// Setup officiel de react-native-gesture-handler
import "react-native-gesture-handler/jestSetup";

// Mock du module Animated pour éviter les warnings liés aux animations
jest.mock("react-native/Libraries/Animated/NativeAnimatedHelper");

// Mock basique de react-navigation pour éviter les erreurs liées à la navigation
jest.mock("@react-navigation/native", () => {
  return {
    ...jest.requireActual("@react-navigation/native"),
    useNavigation: () => ({
      navigate: jest.fn(),
      goBack: jest.fn(),
    }),
  };
});

// Mock de react-native-reanimated pour éviter les erreurs en test
jest.mock("react-native-reanimated", () => {
  const Reanimated = require("react-native-reanimated/mock");

  // Override timing for synchronous animations
  Reanimated.default.timing = () => {
    return {
      start: (cb) => {
        if (cb) cb();
      },
    };
  };

  return Reanimated;
});
